.accordion {
  max-width: 600px;
  margin: 24px auto 0;
  padding-bottom: 30px;
}

.accordion_list + .accordion_list {
  margin-top: 12px;
}

.accordion_list {
  border: 2px solid #4A3636;
  border-radius: 12px;
  overflow: hidden;
}

.accordion_area {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background-color: #fff;
  border-radius: 12px 12px 0 0;
  cursor: pointer;
}

.accordion_area:focus {
  border: 2px solid #67B0C7;
  border-radius: 10px;
}

.accordion_q {
  font-family: "Josefin Sans", sans-serif;
  color: #9ED0E0;
  font-size: 1.5rem;
  line-height: 1.25;
  display: block;
}

.accordion_title {
  font-size: 0.875rem;
  font-weight: 700;
  color: #4A3636;
  line-height: 1.6;
  text-align: left;
  transition: all 0.4s ease;
}

.accordion_toggle {
  width: 1.625rem;
  height: 1.625rem;
  background-color: #9ED0E0;
  border-radius: 50%;
  position: relative;
  display: block;
  margin-left: auto;
}

/* ＋部分 */
.accordion_toggle::before,
.accordion_toggle::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease;
  border-radius: 50px;
}

.accordion_toggle::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
}

.accordion_toggle::after {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.accordion_toggle.close::before {
  transform: translate(-50%, -50%);
}

/* 開く部分 */
.accordion_box {
  display: none;
  padding: 1rem;
}

.accordion_box_contents {
  display: flex;
  align-items: center;
  gap: 1rem;
}

